import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { useNavigate } from 'react-router-dom';
import MapSvg from "./MapSvg";
import '../css/TravelDate.css'; // CSS  import

//  'yyyy-MM-dd'   
const formatDate = (date) => {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
};

const TravelDate = () => {
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);
    const [destination, setDestination] = useState('');
    const [departure, setDeparture] = useState('');
    const navigate = useNavigate();

    const handleTravel = () => {
        const formattedStartDate = startDate ? formatDate(startDate) : '';
        const formattedEndDate = endDate ? formatDate(endDate) : '';
        const params = new URLSearchParams({
            startDate: formattedStartDate,
            endDate: formattedEndDate,
            departure,
            destination
        });
        navigate(`/details?${params.toString()}`);
    };

    const cities = [
        "", "", "", "", "", "", "",
        "", "", "", "", "", "", "",
        "", "", ""
    ];

    return (
        <div className="form">
            <div className="left-panel">
                <MapSvg onCityClick={setDestination}/>
            </div>
            <div className="right-panel">
                <div className="form-item">
                    <label htmlFor="startDate" className="label">Start Date:</label>
                    <DatePicker
                        id="startDate"
                        selected={startDate}
                        onChange={date => setStartDate(date)}
                        dateFormat="yyyy-MM-dd"
                        className="input-field" //  
                    />
                </div>
                <div className="form-item">
                    <label htmlFor="endDate" className="label">End Date:</label>
                    <DatePicker
                        id="endDate"
                        selected={endDate}
                        onChange={date => setEndDate(date)}
                        dateFormat="yyyy-MM-dd"
                        className="input-field" //  
                    />
                </div>
                <div className="form-item">
                    <label htmlFor="departure" className="label">Departure:</label>
                    <select
                        id="departure"
                        value={departure}
                        onChange={e => setDeparture(e.target.value)}
                        className="departure-input" //  
                    >
                        <option value=""> </option>
                        {cities.map(city => (
                            <option key={city} value={city}>{city}</option>
                        ))}
                    </select>
                </div>
                <div className="form-item">
                    <label htmlFor="destination" className="label">Destination:</label>
                    <input
                        type="text"
                        id="destination"
                        value={destination}
                        onChange={e => setDestination(e.target.value)}
                        className="destination-input" //  
                    />
                </div>

                <button onClick={handleTravel} className="button"></button> {/*   */}
            </div>
        </div>
    );
};

export default TravelDate;
